import Taj from "../assets/2.jpg";
import two from "../assets/3.jpg";
import Three from "../assets/kas.jpg";
import four from "../assets/kas2.jpg";
import DestinationData from "./DestinationData";
import "./DestinationStyles.css";
const Destination = () => {
  return (
    <div className="destination">
      <h1>Popular Destinations</h1>
      <p1>
        Tours give you the opportunity to explore a lot, within a time frame .
      </p1>
      <DestinationData
        className="first-des"
        heading="Taj Mahal , Agra"
        text="The Taj Mahal is an ivory-white marble mausoleum on the south bank of the Yamuna river in the Indian city of Agra. It was commissioned in 1632 by the Mughal emperor,
      Shah Jahan (reigned from 1628 to 1658), to house the tomb of his favourite wife, Mumtaz Mahal.
       The tomb is the centrepiece of a 17-hectare (42-acre)complex, which includes a mosque and a guest house, and 
       et in formal gardens bounded on three sides by a crenellated wall."
        img1={Taj}
        img2={two}
      />

      <DestinationData
        className="first-des-reverse"
        heading="Kashmir"
        text="The valley of Kashmir is as rich with history and 
      political controversy as it is with culture and natural 
      phenomena. Sample exquisitely spiced native cuisines and 
      festive teas, then walk off your meal along the rugged 
      trekking routes to the north. Marvel at the famous 
      houseboats of Srinagar and take a spiritual moment to 
      reflect at one of the many pilgrimage sites and religious 
      shrines that dot the region. Of course, native craftsmanship makes for excellent
       souvenirs—carpets and textiles are an especial shopping must."
        img1={Three}
        img2={four}
      />
    </div>
  );
};

export default Destination;
