import "./TripStyles.css";
import TripData from "./TripData";
import Trip1 from "../assets/sakleshpur.jpg";
import Trip2 from "../assets/punjab.jpg";
import Trip3 from "../assets/Mahabalipuram.jpg";
function Trip() {
  return (
    <div className="trip">
      <h1>Recent Trips</h1>
      <p> You can discover amazing places using Google Maps.</p>
      <div className="tripcard">
        <TripData
          image={Trip1}
          heading="Trip in Sakleshpur"
          text="A hill station in the heights of Western Ghats
         of India, Sakleshpur is a small town located in the 
         state of Karnataka. Famous for being surrounded by a 
         surprising number of plantations of coffee, tea and spices,
         leshpur’s economy thrives on these. Apart from that, 
         with the recent development in the tourism happening around
         the town, places to visit in Sakleshpur have recently 
         gathered a lot of fame for themselves because of their beauty and the kind of experiences that they harbour,
         both of which are unparalleled"
        />
        <TripData
          image={Trip2}
          heading="Trip in Amritsar"
          text="The Golden Temple Amritsar India (Sri Harimandir Sahib Amritsar) is not only a central religious place of the Sikhs, but also a symbol of human brotherhood and equality. Everybody, irrespective of cast, creed or race can seek spiritual solace and religious fulfilment without any hindrance. It also represents the distinct identity, glory and heritage of the Sikhs."
        />
        <TripData
          image={Trip3}
          heading="Trip in Mahabalipuram"
          text="Built during the 7th century, Shore Temple is one of the oldest South Indian temples constructed in the Dravidian style and depicts the royal taste of the Pallava dynasty. The work of the temple has been listed amongst the World Heritage Sites by UNESCO. It is located in Mahabalipuram and is one of the most photographed monuments in India situated on the shores of Bay of Bengal"
        />
      </div>
    </div>
  );
}

export default Trip;
